<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="info"
        center
        show-icon
        :closable="false"
      >
      </el-alert>
      <!--  -->
      <el-steps
        :space="200"
        :active="activeindex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeindex"
          :tab-position="'left'"
          :before-leave="beforeleave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                expand-trigger="hover"
                :options="catelist"
                :props="cateProps"
                v-model="addForm.goods_cat"
                @change="handlechange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTabledata"
              :key="item.attr_id"
            >
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  border
                  :label="cb"
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- actionapion-previewremove -->
            <el-upload
              :headers="headerObj"
              action="https://www.liulongbin.top:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.good_introduce">

            </quill-editor>
            <el-button type="primary" style="margin-top:10px" @click="add"></el-button>

          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- // -->
    <el-dialog
      title=""
      :visible.sync="previewdialogVisible"
      width="50%"
    >
      <img :src = "previewPath" alt="" class="imgpreview">
     
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      activeindex: "0",
      //
      addFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      //
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        //
        goods_cat: [],
        //
        pics: [],
        //
        good_introduce:'',
        //
        attrs:[]
      },
      //
      catelist: [],
      //
      cateProps: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
      },
      //
      manyTabledata: [],
      //
      onlyTableData: [],
      //E UIaxiostoken 
      headerObj: {
        Authorization: window.sessionStorage.getItem("token"),
      },
      //
      previewPath: "",
      //
      previewdialogVisible:false,

    };
  },
  created() {
    this.getCateList();
  },
  computed: {
    getcateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2];
      }
      return null;
    },
  },
  methods: {
    //
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) return this.$message.error("");
      this.catelist = res.data;
    },
    //
    handlechange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = [];
      }
    },
    //
    beforeleave(activeName, oldactiveName) {
      if (oldactiveName === "0" && this.addForm.goods_cat.length !== 3) {
        this.$message.error("");
        return false;
      }
    },
    //tab
    async tabClicked() {
      //1 
      if (this.activeindex === "1") {
        const { data: res } = await this.$http.get(
          `categories/${this.getcateId}/attributes`,
          { params: { sel: "many" } }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");

        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(" ");
        });
        this.manyTabledata = res.data;
      } else if (this.activeindex == "2") {
        const { data: res } = await this.$http.get(
          `categories/${this.getcateId}/attributes`,
          { params: { sel: "only" } }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");
       
        this.onlyTableData = res.data;
      }
    },
    //
    handlePreview(file) {
      this.previewPath = file.response.data.url;
      this.previewdialogVisible = true
    },
    //
    handleRemove(file) {
      //
      const filePath = file.response.data.tmp_path;
      //
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath);
      //
      this.addForm.pics.splice(i, 1);
      
    },
    //
    handleSuccess(response) {
      //
      const picINfo = { pic: response.data.tmp_path };
      //push
      this.addForm.pics.push(picINfo);
      
    },
    //
    add(){
      //
      this.$refs.addFormRef.validate(async valid =>{
        if(!valid){
          return this.$message.error('')
        }
        //
        //good_cat
        // ladash cloneDeepobj
       const form =  _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        //
        this.manyTabledata.forEach(item => {
          const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals.join(' ')}
          this.addForm.attrs.push(newInfo)
        })
        this.onlyTableData.forEach(item =>{
          const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals}
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)
        //

        const {data:res} = await this.$http.post('goods',form)
        if(res.meta.status !== 201) 
            return this.$message.error("");
           this.$message.success("");
           this.$router.push('/goods')
      })
    },
  },
};
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.imgpreview{
  width: 100%;
}
</style>