<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert
        :closable="false"
        title=""
        type="warning"
        show-icon
      >
      </el-alert>
      <el-row class="elrow">
        <el-col>
          <span></span>
          <el-cascader
            expand-trigger="hover"
            v-model="selectedcateKeys"
            :options="catelist"
            :props="cateProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isBtn"
            @click="addDialogVisiable = true"
            ></el-button
          >
          <!--  -->
          <el-table :data="manyTableDate" border stripe>
            <!--  -->
            <el-table-column type="expand"></el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="deletParams(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isBtn"
            @click="addDialogVisiable = true"
            ></el-button
          >
          <!--  -->
          <el-table :data="onlyTableDate" border stripe>
            <!--  -->
            <el-table-column type="expand"></el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="deletParams(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="addDialogVisiable"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        ref="addFormRef"
        :model="addForm"
        label-width="80px"
        :rules="rule"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisiable = false"> </el-button>
        <el-button type="primary" @click="addparams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="editDialogVisiable"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        label-width="80px"
        :rules="rule"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisiable = false"> </el-button>
        <el-button type="primary" @click="editparams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      catelist: [],
      // 
      cateProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      //
      selectedcateKeys: [],
      //
      activeName: "many",
      //
      manyTableDate: [],
      //
      onlyTableDate: [],
      //
      addDialogVisiable: false,
      //
      editDialogVisiable: false,
      //
      addForm: {},
      //
      editForm: {},

      //
      rule: {
        attr_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    isBtn() {
      if (this.selectedcateKeys.length !== 3) {
        return true;
      }
      return false;
    },
    //ID
    cateID() {
      if (this.selectedcateKeys.length === 3) {
        return this.selectedcateKeys[2];
      }
      return null;
    },
    //
    titleText() {
      if (this.activeName === "many") return "";
      return "";
    },
  },
  created() {
    //
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.catelist = res.data;
    },
    //
    handleChange() {
      this.getParmasDate();
    },
    //tab
    handleClick() {
      this.getParmasDate();
    },
    //
    async getParmasDate() {
      //
      if (this.selectedcateKeys.length !== 3) {
        this.selectedcateKeys = [];
        this.manyTableDate = [];
        this.onlyTableDate = [];
        return;
      }
      //
      const { data: res } = await this.$http.get(
        `categories/${this.cateID}/attributes`,
        { params: { sel: this.activeName } }
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      if (this.activeName === "many") this.manyTableDate = res.data;
      else this.onlyTableDate = res.data;
    },
    //
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    //
    addparams() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          `categories/${this.cateID}/attributes`,
          { attr_name: this.addForm.attr_name, attr_sel: this.activeName }
        );
        if (res.meta.status !== 201) return this.$message.error("");
        this.$message.success("");
        this.addDialogVisiable = false;
        //
        this.getParmasDate();
      });
    },
    //
    async showEditDialog(attr_id) {
      const { data: res } = await this.$http.get(
        `categories/${this.cateID}/attributes/${attr_id}`,
        { params: { attr_sel: this.activeName } }
      );
      if (res.meta.status !== 200) return this.$message.error("!");
      this.editForm = res.data;
      this.editDialogVisiable = true;
    },
    //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //
    async editparams() {
      const { data: res } = await this.$http.put(
        `categories/${this.cateID}/attributes/${this.editForm.attr_id}`,
        { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getParmasDate();
      this.editDialogVisiable = false;
    },
    //
    async deletParams(attr_id) {
      //
      const re = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      // cancel
      if (re !== "confirm") return this.$message.info("");

      const { data: res } = await this.$http.delete(
        `categories/${this.cateID}/attributes/${attr_id}`
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getParmasDate();
    },
  },
};
</script>

<style lang="less" scoped>
.elrow {
  margin: 15px 0;
}
</style>