<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            v-model="paperinfo.query"
            placeholder=""
            clearable
            @clear="getpaperList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getpaperList"
            ></el-button> </el-input
        ></el-col>
      </el-row>
      <!--  -->
      <el-table :data="paperList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column
          label="()"
          prop="order_price"
          width="120px"
        ></el-table-column>
        <el-table-column label="" prop="pay_status" width="90px">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="is_send"
          width="80px"
        ></el-table-column>
        <el-table-column label="" prop="create_time" width="160px">
          <template slot-scope="scope">
            {{ scope.row.create_time | dataFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="220px">
          <template slot-scope="">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="showBox"
              ></el-button
            >
            <el-button type="success" size="mini" icon="el-icon-location" @click="showprogressBox"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paperinfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="paperinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
    <!-- // -->
    <el-dialog title="" :visible.sync="addressVisiable" width="50%" @close = "addressDialogClosed">
      <el-form
        :model="addressForm"
        :rules="rules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1">

          </el-cascader>
        </el-form-item>
          <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisiable = false,addressDialogClosed"> </el-button>
        <el-button type="primary" @click="addressVisiable = false,addressDialogClosed"
          > </el-button
        >
      </span>
    </el-dialog>
    <!-- // -->
    <el-dialog
  title=""
  :visible.sync="progressdialogVisible"
  width="50%"
  >
   <el-timeline >
    <el-timeline-item
      v-for="(activity, index) in progressInfo"
      :key="index"
      :timestamp="activity.time">
      {{activity.context}}
    </el-timeline-item>
  </el-timeline>
  
</el-dialog>
  </div>
</template>

<script>
import cityData from '@/js/citydata.js'

export default {
  data() {
    return {
      //
      paperinfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      //
      paperList: [],
      //
      total: 0,
      //
      addressVisiable: false,
      progressdialogVisible:false,
      //
      addressForm:{
          address1:[],
          address2:'',
      },
      //
      rules:{
          address1:[{required: true, message: '/', trigger: 'blur'}],
         address2:[{required: true, message: '', trigger: 'blur'}]
      
      },
      //
      cityData,
      //
      progressInfo:[],

    };
  },
  created() {
    this.getpaperList();
  },
  methods: {
    //
    async getpaperList() {
      const { data: res } = await this.$http.get("orders", {
        params: this.paperinfo,
      });
      
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.paperList = res.data.goods;
      this.total = res.data.total;
      
    },
    //pagesizi
    handleSizeChange(newSize) {
      this.paperinfo.pagesize = newSize;
      this.getpaperList();
    },
    //pagenum
    handleCurrentChange(newNum) {
      this.paperinfo.pagenum = newNum;
      this.getpaperList();
    },
    //
    showBox() {
        this.addressVisiable = true
    },
    //
    addressDialogClosed(){
        this.$refs.addFormRef.resetFields()
    },

   async showprogressBox(){
       const {data:res} = await this.$http.get('/kuaidi/:id')
         if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.progressInfo = res.data
     this.progressdialogVisible = true
    }
  },
};
</script>

<style lang="less" scoped>
.el-cascader{
    width: 100%;
}
</style>