<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col
          ><el-button type="primary" @click="AddRolesdialogVisible = true"
            ></el-button
          ></el-col
        >
      </el-row>
      <!--  -->
      <el-table :data="rolesList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!--  -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <!--  -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editbackRoleinfo(scope.row.id)"
              ></el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delet"
              size="mini"
              @click="DeletRoleinfo(scope.row.id)"
              ></el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showdialogVisible(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="AddRolesdialogVisible"
      width="50%"
    >
      <!--  -->
      <el-form :model="AddForm" label-width="70px">
        <el-form-item label="">
          <el-input v-model="AddForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="AddForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="AddRolesdialogVisible = false"> </el-button>
        <el-button type="primary" @click="AddRolesinfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="EditRolesdialogVisible" width="50%">
      <el-form :model="AddForm" label-width="70px">
        <el-form-item label="">
          <el-input v-model="chagneForm.roleName" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="chagneForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditRolesdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleinfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="FpdialogVisible"
      @close="clearKeys"
      width="50%"
    >
      <el-tree
        :data="rightList"
        :props="treeprops"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defkeys"
        ref="treeRef"
      >
      </el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="FpdialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRight"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      rolesList: [],
      //
      FpdialogVisible: false,
      //
      AddRolesdialogVisible: false,
      //
      EditRolesdialogVisible: false,
      //
      rightList: [],
      //
      treeprops: {
        children: "children",
        label: "authName",
      },
      //ID
      defkeys: [],
      //ID
      roleId: "",
      //
      AddForm: {
        roleName: "",
        roleDesc: "",
      },
      chagneForm: [],
    };
  },
  created() {
    //
    this.getrolesList();
  },
  methods: {
    async getrolesList() {
      const { data: res } = await this.$http.get("/roles");
      if (res.meta.status !== 200)
        return this.$message.error("!");
      this.rolesList = res.data;
    },
    async removeRightById(role, right) {
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);

      if (confirmResult !== "confirm") return this.$message.info("");

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${right}`
      );
      if (res.meta.status !== 200) return this.$message.error("");
      role.children = res.data;
    },
    async showdialogVisible(Role) {
      this.roleId = Role.id;
      //
      const { data: res } = await this.$http.get("rights/tree");

      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.rightList = res.data;
      //
      this.getRefKeys(Role, this.defkeys);

      this.FpdialogVisible = true;
    },

    // Refkeys
    getRefKeys(node, arr) {
      //
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => {
        this.getRefKeys(item, arr);
      });
    },
    clearKeys() {
      this.defkeys = [];
    },
    //
    async allotRight() {
      const key = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      const idStr = key.join(",");
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");

      this.getrolesList();
      this.FpdialogVisible = false;
    },
    //
    async AddRolesinfo() {
      const { data: res } = await this.$http.post("roles", this.AddForm);
      if (res.meta.status !== 201) return this.$message.error("");
      this.$message.success("");
      this.getrolesList();
      this.AddRolesdialogVisible = false;
    },
    //
    async editbackRoleinfo(id) {
      this.EditRolesdialogVisible = true;
      const { data: res } = await this.$http.get("/roles/" + id);
      if (res.meta.status !== 200)
        return this.$message.error("");
      this.chagneForm = res.data;
    },
    // 
    async editRoleinfo() {
      const { data: res } = await this.$http.put(
        `roles/${this.chagneForm.roleId}/`,
        {
          roleName: this.chagneForm.roleName,
          roleDesc: this.chagneForm.roleDesc,
        }
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");

      //
      this.EditRolesdialogVisible = false;
      //
      this.getrolesList();
    },
    //
    async DeletRoleinfo(id) {
      const re = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      // cancel
      if (re !== "confirm") return this.$message.info("");
      const { data: res } = await this.$http.delete("/roles/" + id);
      if (res.meta.status !== 200) return this.$message.error("!");
      this.$message.success("");
      //
      this.getrolesList();
    },
  },
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>