<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            v-model="Listqury.query"
            placeholder=""
            class="input-with-select"
            clearable
            @clear="getGoodList()"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getGoodList()"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="GoodList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column
          label="()"
          prop="goods_price"
          width="120px"
        ></el-table-column>
        <el-table-column label="" prop="goods_weight" width="80px"></el-table-column>
        <el-table-column label="" prop="add_time" width="140px">
            <template slot-scope="scope">
                {{scope.row.add_time | dataFormat}}
            </template>
        </el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="">
            <el-button type="primary" size="mini" icon="el-icon-edit"
              ></el-button
            >
            <el-button type="danger" size="mini" icon="el-icon-delete"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
        <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="Listqury.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="Listqury.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      Listqury: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      //
      GoodList: [],
      //
      total: 0,
    };
  },
  created() {
    this.getGoodList();
  },
  methods: {
    //
    async getGoodList() {
      const { data: res } = await this.$http.get("goods", {
        params: this.Listqury,
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.GoodList = res.data.goods;
      this.total = res.data.total;
    },
    //pagesizi
    handleSizeChange(newSize){
        this.Listqury.pagesize = newSize
        this.getGoodList()
    },
    //pagenum
    handleCurrentChange(newNum){
        this.Listqury.pagenum = newNum
        this.getGoodList()
    },
    //
    goAddpage(){
        this.$router.push('/goods/add')
    }
  },
};
</script>

<style lang="less" scoped>
</style>