<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-button type="primary" @click="showAddcategoryVisiable()"
        ></el-button
      >
      <!--  -->
      <tree-table class="tree-table"
        :data="Catelist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
      <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" style="color:lightgreen;" v-if="scope.row.cat_deleted === false"></i>
          <i class="el-icon-error" style="color:red;" v-else></i>
      </template>
           
        <template slot="level" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
            <el-tag v-else-if="scope.row.cat_level === 1" type="success"></el-tag>
            <el-tag v-else type="warning"></el-tag>
      </template>
      
         <template slot="opert" slot-scope="scope">
             <el-button type="primary" icon="el-icon-edit" size="mini" @click="findIdCateinfo(scope.row.cat_id)"></el-button>
             <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeIdCateinfo(scope.row.cat_id)"></el-button>
      </template>

      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="CateListinfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="CateListinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="AddcategoryVisiable"
  width="30%"
  @close="addCateDiglogClose"
  >
  <el-form :model="addCateFrom" :rules="rules" ref="addCateFromRef" label-width="100px">
  <el-form-item label=":" prop="cat_name">
    <el-input v-model="addCateFrom.cat_name"></el-input>
  </el-form-item>
   <el-form-item label=":">
    <!-- options -->
    <!-- props -->
  <el-cascader
    expand-trigger="hover"
    v-model="selectedKeys"
    :options="parentCatellist"
    :props="cascaderprops"
    clearable
    change-on-select
    @change="parentCatechange"></el-cascader>
    </el-form-item>
  </el-form>

  <span slot="footer" class="dialog-footer">
    <el-button @click="AddcategoryVisiable = false"> </el-button>
    <el-button type="primary" @click="addcate"> </el-button>
  </span>
</el-dialog>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="editCatedialogVisible"
  width="50%"
  >
  <el-form :model="changeCateFrom" :rules="rules" label-width="100px">
  <el-form-item label=":">
    <el-input v-model="changeCateFrom.cat_name"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editCatedialogVisible = false"> </el-button>
    <el-button type="primary" @click="Editcate"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Catelist: [],
      //
      CateListinfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      //
      addCateFrom:{
          //
          cat_name:'',
          //ID
          cat_pid:0,
          //0
          cat_level:0
      },
      total: 0,
      //
      AddcategoryVisiable:false,
      //
      editCatedialogVisible:false,
      //Table
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        {   
            // 
            label:"",
            type:"template",
            template:'isok'
        },
        {   
            //2 
            label:"",
            type:"template",
            template:'level'
        },
         {   
            //2 
            label:"",
            type:"template",
            template:'opert'
        },
      ],
      //
      rules:{
        cat_name:[
             { required: true, message: '', trigger: 'blur' },
        ]
      },
      //
      parentCatellist:[],
      //
      cascaderprops:{
          value:'cat_id',
          label:'cat_name',
          children:'children'
      },
      //ID
      selectedKeys:[],
      //
      changeCateFrom:{},
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: this.CateListinfo,
      });
      if (res.meta.status !== 200) return this.$message.error("!");
      this.Catelist = res.data.result;
      this.total = res.data.total;
    },
    addDialogClosed() {
      this.AddcategoryVisiable = ture;
    },
    //pagesize
    handleSizeChange(newSize) {
        this.CateListinfo.pagesize = newSize
        this.getCateList()
    },
    //pagenum
    handleCurrentChange(newNum) {
        this.CateListinfo.pagenum = newNum
        this.getCateList()
    },
    showAddcategoryVisiable(){
        //
        this.getParentCateList()
        this.AddcategoryVisiable = true
    },
    //
  async  getParentCateList(){
      const {data:res} = await  this.$http.get('categories',{params:{
            type:2
        }})
        if(res.meta.status !== 200){
            return this.$message.error('')
        }
        this.parentCatellist = res.data
        
    },
    //
    parentCatechange(){
        //selectedKeys
        if(this.selectedKeys.length > 0){
            this.addCateFrom.cat_pid = this.selectedKeys[
                this.selectedKeys.length -1
            ]
        //
        this.addCateFrom.cat_level = this.selectedKeys.length
        return
        }
        else{
            // 
            this.addCateFrom.cat_pid = 0
            this.addCateFrom.cat_level = 0
        }
       
    },
    //
    addcate(){
        this.$refs.addCateFromRef.validate(async valid =>{
            if(!valid) return
          const {data:res} = await this.$http.post('categories',this.addCateFrom)
          if(res.meta.status !== 201) return this.$message.error('')
         this.$message.success('')
         //
          this.getCateList()
          //
          this.AddcategoryVisiable = false
        })
        
    },
    // 
    addCateDiglogClose(){
        this.$refs.addCateFromRef.resetFields()
        this.selectedKeys = []
        this.addCateFrom.cat_level = 0
        this.addCateFrom.cat_pid = 0
   },
     //ID
 async findIdCateinfo(id){
    
    const {data:res} = await this.$http.get('categories/'+ id)
    if(res.meta.status !== 200) return this.$message.error('!')
    this.changeCateFrom = res.data
    console.log(this.changeCateFrom)
    this.editCatedialogVisible = true
 },
 //
   async Editcate(){
    const {data:res} = await this.$http.put('categories/'+ this.changeCateFrom.cat_id,{cat_name:this.changeCateFrom.cat_name})
     if(res.meta.status !== 200) return this.$message.error('')
     this.$message.success('')
     this.getCateList()
     this.editCatedialogVisible = false
 },
  //ID
    async removeIdCateinfo(id) {
      //
      const re = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      // cancel
      if (re !== "confirm") return this.$message.info("");
      const { data: res } = await this.$http.delete("categories/" + id);
      if (res.meta.status !== 200) return this.$message.error("!");
      this.$message.success("");
      //
      this.getCateList();
    },
  },

  };
</script>

<style lang="less" scoped>
.tree-table{
    margin-top: 15px;
}
.el-cascader{
    width: 100%;
}
</style>