<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            v-model="queryinfo.query"
            placeholder=""
            class="input-with-select"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisable = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label="" width="180">
        </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column prop="mg_state" label="">
          <!--  -->
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="userstatechanged(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              v-model="scope.row"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="changeUserinfo(scope.row.id)"
            >
            </el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserbyId(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRoles(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryinfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisable"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->

      <el-form
        :model="addFrom"
        :rules="rules"
        ref="addfromRef"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addFrom.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addFrom.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addFrom.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addFrom.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisable = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="changeDialogVisable"
      width="50%"
      @close="changeDialogClosed"
    >
      <!--  -->
      <el-form
        :model="chagneForm"
        :rules="rules"
        ref="changefromRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input v-model="chagneForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="chagneForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="chagneForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeDialogVisable = false"> </el-button>
        <el-button type="primary" @click="changeUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="setRolesdialogVisible" width="50%" @close="setRolesdialogClosed">
      <div>
        <p>{{ userinfo.username }}</p>
        <p>{{ userinfo.role_name }}</p>
        <p>
          
          <el-select v-model="selctedRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolesdialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryinfo: {
        query: "",
        pagenum: 1,
        pagesize: 2,
      },
      userlist: [],
      total: 0,
      //
      addDialogVisable: false,
      //
      changeDialogVisable: false,
      //
      setRolesdialogVisible: false,
      //
      addFrom: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      //
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 12,
            message: " 6  12 ",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 11,
            max: 11,
            message: "11",
            trigger: "blur",
          },
        ],
      },
      //
      chagneForm: [],
      //
      userinfo: {},
      //
      rolesList: [],
      //ID
      selctedRoleId: '',
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get("/users", {
        params: this.queryinfo,
      });
      if (res.meta.status !== 200) return this.$message.error("");
      this.userlist = res.data.users;
      this.total = res.data.total;
      console.log(this.userlist)
    },
    //  
    handleSizeChange(newSize) {
      this.queryinfo.pagesize = newSize;
      this.getUserList();
    },
    //  
    handleCurrentChange(newPage) {
      this.queryinfo.pagenum = newPage;
      this.getUserList();
    },
    async userstatechanged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      );
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state;
        return this.$message.error("!");
      }

      this.$message.success("");
    },
    //
    addDialogClosed() {
      this.$refs.addfromRef.resetFields();
    },
    changeDialogClosed() {
      this.$refs.changefromRef.resetFields();
    },
    addUser() {
      this.$refs.addfromRef.validate(async (valid) => {
        if (!valid) return;
        //
        const { data: res } = await this.$http.post("/users", this.addFrom);
        if (res.meta.status !== 201)
          return this.$message.error("");
        this.$message.success("");
        //
        this.addDialogVisable = false;
        //
        this.getUserList();
      });
    },
    //
    async changeUserinfo(val) {
      this.changeDialogVisable = true;
      const { data: res } = await this.$http.get("/users/" + val);
      if (res.meta.status !== 200)
        return this.$message.error("");
      this.chagneForm = res.data;
    },
    //
    changeUser() {
      this.$refs.changefromRef.validate(async (valid) => {
        if (!valid) return;
        //
        const { data: res } = await this.$http.put(
          "/users/" + this.chagneForm.id,
          { email: this.chagneForm.email, mobile: this.chagneForm.mobile }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");
        this.$message.success("");
        //
        this.changeDialogVisable = false;
        //
        this.getUserList();
      });
    },
    //ID
    async removeUserbyId(id) {
      //
      const re = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      // cancel
      if (re !== "confirm") return this.$message.info("");
      const { data: res } = await this.$http.delete("/users/" + id);
      if (res.meta.status !== 200) return this.$message.error("!");
      this.$message.success("");
      //
      this.getUserList();
    },
    //
    async setRoles(roleinfo) {
      this.userinfo = roleinfo;
      //
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200)
        return this.$message.error("!");
      this.$message.success("");
      this.rolesList = res.data;
      this.setRolesdialogVisible = true;
    },
    //
    async saveRoleInfo() {
      if (!this.selctedRoleId) {
        return this.$message.error("");
      }
      const { data: res } = await this.$http.put(
        
        `users/${this.userinfo.id}/role`,
        { rid: this.selctedRoleId }
      );
      if (res.meta.status !== 200) return this.$message.error("!");
      this.$message.success("");
       
      this.setRolesdialogVisible = false;
     this.getUserList();
  },
    //
    setRolesdialogClosed(){
      this.selctedRoleId =''
      this.userinfo ={}
    }
  },
};
</script>

<style lang="less" scoped>
</style>